.c-toast {
  position: fixed;
  z-index: 10001;
  top: 0;
  left: 0;
  width: 100%;
  height: 0
}
.c-toast .toast {
  margin: 0 0 0.25rem;
  padding: 0.5rem;
  min-width: 12rem;
  background: white
}
.c-toast .toast .code {}
.c-toast .toast .code:before {
  content: "code: "
}
.c-toast .toast .message {
  margin-top: 0.5rem;
  max-height: 16rem;
  overflow: auto
}
.c-toast .toast .message:before {
  content: "message: "
}



