body, h1, h2, h3, h4, h5, h6, p, dl, dd, form, ul, ol, pre, blockquote, textarea, input, figure {
  margin: 0;
}

ul, ol, th, td, button, textarea, input {
  padding: 0;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

article, footer, header, nav, section {
  display: block;
}

button, input, textarea {
  outline: none;
}

:-ms-input-placeholder {
  color: rgb(153, 153, 153);
}

html {
  font: 1em/1.5 Tahoma, Arial, Helvetica, "Microsoft YaHei", "宋体", sans-serif;
}

body {
  font-size: 1rem;
  color: rgb(51, 51, 51);
  background-color: #FBF9FE
}

body ::-webkit-scrollbar {
  width: 0.3rem;
  height: 0.3rem;
  background-color: rgba(255, 255, 255, 0.3);
}

body ::-webkit-scrollbar-thumb {
  background-color: #E7604A;
}

body ::-webkit-scrollbar-button {
  height: 0;
  width: 0;
  background-color: transparent;
}

input, textarea, keygen, select, button {
  font: 1rem/1.5 Tahoma, Arial, Helvetica, "Microsoft YaHei", "宋体", sans-serif;
  color: rgb(51, 51, 51);
}

code, kbd, pre, samp {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
}

pre code {
  padding: 0;
  font-size: inherit;
  color: inherit;
  white-space: pre-wrap;
  background-color: transparent;
  border-radius: 0;
}

a {
  color: rgb(51, 51, 51);
  text-decoration: underline
}

a:hover {
  text-decoration: none;
}

a:active {
  color: #17A0B5;
}

.link-active {
  color: #E7604A;
}

button {
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  white-space: nowrap;
}

.link {
  color: #17A0B5
}

.link:active {
  color: #E7604A;
}

.link:disabled {
  cursor: not-allowed;
  color: rgb(153, 153, 153);
}

.button {
  width: 100%;
  line-height: 3;
  color: white;
  background-color: rgb(179, 179, 179);
  border-radius: 0.25rem
}

.button:active {
  background-color: rgb(204, 204, 204);
}

.button.primary {
  background-color: #17A0B5;
}

.button.primary:active {
  background-color: rgb(29, 199, 226);
}

.button.primary:disabled {
  cursor: not-allowed;
  color: rgb(179, 179, 179);
  background-color: rgb(204, 204, 204);
}

.button.warning {
  background-color: #E7604A;
}

.button.warning:active {
  background-color: rgb(237, 135, 120);
}

.button.warning:disabled {
  cursor: not-allowed;
  color: rgb(179, 179, 179);
  background-color: rgb(204, 204, 204);
}

.button.default {
  color: rgb(51, 51, 51);
  background-color: rgb(204, 204, 204);
}

.button.default:active {
  background-color: rgb(230, 230, 230);
}

.button.default:disabled {
  cursor: not-allowed;
  color: rgb(179, 179, 179);
  background-color: rgb(204, 204, 204);
}

.slide-transition {
  transition: all .5s ease;
}

.slide-enter, .slide-leave {
  opacity: 0;
  transform: translate3d(0, -1rem, 0);
}

.slide-up-transition {
  transition: all .5s ease;
}

.slide-up-enter, .slide-up-leave {
  opacity: 0;
  transform: translate3d(0, 1rem, 0);
}

.fade-transition {
  transition: all .5s ease;
}

.fade-enter, .fade-leave {
  opacity: 0;
}

