.header {
  position: fixed;
  z-index: 11;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background-color: rgb(6, 40, 45);
}

.router-view {
  transition: all .5s ease;
}

.router-view-transition-enter,
.router-view-transition-leave {
