.c-navbar {
  position: absolute;
  z-index: 11;
  width: 100%;
}

.c-navbar-toggle {
  position: absolute;
  z-index: 102;
  right: 0;
  width: 4rem;
  color: white;
  font-size: 1.2rem;
  line-height: 4rem;
  text-align: center
}

.c-navbar-toggle.active {
  color: #E7604A;
}

.c-navbar-menu {
  position: absolute;
  z-index: 103;
  top: 4rem;
  width: 100%;
  background-color: white;
  box-shadow: 0 1px 3px rgb(204, 204, 204)
}

.c-navbar-menu li {}

.c-navbar-menu li ul {
  display: none;
}

.c-navbar-menu li li {
  display: block;
}

.c-navbar-menu li li a {
  display: block;
}

.c-navbar-menu li:active {}

.c-navbar-menu li:active ul {
  display: block;
  background-color: rgb(230, 230, 230);
}

.c-navbar-menu a {
  display: block;
  padding: 0 1rem;
  line-height: 4rem;
  text-decoration: none;
  text-align: center;
}

.c-navbar-menu a:active {
  background-color: rgb(242, 242, 242);
}

.c-navbar-menu a.link-active {
  color: rgb(18, 120, 135);
  background-color: rgb(242, 242, 242);
}

.c-navbar-menu a.link-active + ul {
  display: block;
  background-color: rgb(230, 230, 230);
}

.c-navbar-menu a.link-active + ul a {}

.c-navbar-menu a.link-active + ul a.link-active {
  color: #17A0B5;
  background-color: rgb(242, 242, 242);
}



